import React, { useState } from 'react'
import styled from 'styled-components'
import { MdClose } from 'react-icons/md'
import axios from 'axios'
import querystring from 'query-string'
import validator from 'validator'

import { Box, Flex, H3, Text, Button } from '@components/UI'
import {
  height,
  space,
  borders,
  borderWidths,
  borderStyles,
  radii
} from 'styled-system'

const Container = styled(Box).attrs({
  p: 3,
  backgroundColor: 'white'
})`
  position: fixed;
  bottom: 0px;
  width: 100%;
  z-index: 10000;
`

const Content = styled(Flex).attrs({
  justifyContent: 'center'
})``

const TextInput = styled.input.attrs(({ valid }: { valid: boolean }) => ({
  type: 'text',
  mr: 3,
  p: 3,
  my: 'auto',
  height: 3,
  borderRadius: '4px',
  borderStyle: 'solid',
  borderColor: valid ? 'dark' : 'primary',
  borderWidth: 0
}))`
  :focus {
    outline: 0;
  }
  ${height}
  ${space}
  ${borders}
  ${borderWidths}
  ${borderStyles}
  ${radii}
  font-size: 16px;
`

const EmailInput = styled(TextInput).attrs({
  type: 'email'
})``

const Message = styled(Text).attrs({
  my: 'auto',
  pr: 3,
  color: 'black',
  fontSize: 2,
  fontWeight: 700
})``

