const SubscribeButton = styled(Button).attrs(
  ({ displayDisabled, disabled }) => ({
    backgroundColor: displayDisabled ? 'dark' : 'primary',
    type: 'submit',
    name: 'submit',
    id: 'submit',
    style: { cursor: disabled ? null : 'pointer' }
  })
)``

const CloseButton = ({ onClick }: { onClick: Function }) => {
  const Icon = styled(MdClose).attrs({
    fontSize: 24
  })`
    position: relative;
    margin-left: 18px;
    margin-top: 18px;
  `

  const Wrap = styled(Box).attrs({})`
    width: 62px;
    height: 62px;
    position: absolute;
    right: 0px;
    top: 0px;
    cursor: pointer;
  `

  return (
    <Wrap onClick={onClick}>
      <Icon />
    </Wrap>
  )
}

const onSubscribe = async ({
  name,
  email
}: {
  name?: string
  email?: string
}) => {
  const { data } = await axios.post(
    'https://santiandtugce.com/sendy/subscribe',
    querystring.stringify({
      list: '3BTlOk763k9eyahArsrd7wgw',
      name,
      email,
      source: 'santiandtugce.com',
      boolean: true
    }),
    { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
  )

  switch (data) {
    case 1:
      return 'SUCCESS'
    case 'Already subscribed.':
      return 'ALREADY_SUBSCRIBED'
    default:
      return 'FAILED'
  }
}

const useInput = initialValue => {
  const [value, setValue] = useState(initialValue)

  return {
    value,
    setValue,
    reset: () => setValue(''),
    bind: {
      value,
      onChange: event => {
        setValue(event.target.value)
      }
    }
  }
}

const EmailSubscribeForm = () => {
  // SIGNUP, HIDE, SUBSCRIBE_SUCCESS, SUBSCRIBE_FAILED, ALREADY_SUBSCRIBED
  const [barDisplay, setBarDisplay] = useState('SIGNUP')

  const { value: name, bind: bindName, reset: resetName } = useInput('')
  const { value: email, bind: bindEmail, reset: resetEmail } = useInput('')

  const emailOk = validator.isEmail(email)
  const initState = email.length === 0 && name.length === 0
  const validationOk = emailOk || initState

  const onSubscribeCb = ({ result }) => {
    if (result === 'SUCCESS') {
    }
  }

  const SubscribeSuccess = () => {
    setTimeout(() => setBarDisplay('HIDE'), 5000)

    return (
      <Content height="38px">
        <Message>
          {"Thank you! we've sent a confirmation email to your mailbox!"}
        </Message>
        <CloseButton onClick={() => setBarDisplay('HIDE')} />
      </Content>
    )
  }

  const AlreadySubscribed = () => {
    setTimeout(() => {
      resetName()
      resetEmail()
      setBarDisplay('SIGNUP')
    }, 5000)

    return (
      <Content height="38px">
        <Message>{`The email "${email}" is already subscribed!`}</Message>
        <CloseButton onClick={() => setBarDisplay('HIDE')} />
      </Content>
    )
  }

  const SubscribeFailed = () => {
    setTimeout(() => setBarDisplay('SIGNUP'), 3000)

    return (
      <Content height="38px">
        <Message>{`There was a problem... please try again.`}</Message>
        <CloseButton onClick={() => setBarDisplay('HIDE')} />
      </Content>
    )
  }

  return (
    <Container
      style={{ visibility: barDisplay === 'HIDE' ? 'hidden' : 'visible' }}
    >
      {barDisplay === 'SIGNUP' && (
        <Content>
          <Message>Get an email when we release new music!</Message>
          <TextInput valid placeholder="Name" {...bindName} />
          <EmailInput
            valid={validationOk}
            placeholder="Email"
            required
            {...bindEmail}
          />
          <SubscribeButton
            disabled={!emailOk}
            displayDisabled={!validationOk}
            onClick={() =>
              onSubscribe({ name, email }).then(status => {
                if (status === 'SUCCESS') setBarDisplay('SUBSCRIBE_SUCCESS')
                else if (status === 'ALREADY_SUBSCRIBED')
                  setBarDisplay('ALREADY_SUBSCRIBED')
                else if (status === 'FAILED') setBarDisplay('SUBSCRIBE_FAILED')
              })
            }
          >
            Subscribe
          </SubscribeButton>
          <CloseButton onClick={() => setBarDisplay('HIDE')} />
        </Content>
      )}
      {barDisplay === 'SUBSCRIBE_SUCCESS' && <SubscribeSuccess />}
      {barDisplay === 'ALREADY_SUBSCRIBED' && <AlreadySubscribed />}
      {barDisplay === 'SUBSCRIBE_FAILED' && <SubscribeFailed />}
    </Container>
  )
}

export default EmailSubscribeForm
